import React, { FC, memo, useCallback } from 'react';
import classnames from 'classnames/bind';
import { useDispatch } from 'react-redux';

import Button from 'components/Button';
import ClassField from 'pages/Dashboard/ClassField';
import {
  upcomingClassCurriculumPending,
  upcomingClassFeedbackPending,
} from 'store/dashboard/actions';

import styles from './styles.module.scss';

const cx = classnames.bind(styles);

export interface PropsType {
  title: string;
  studentInfo: string;
  enterLink: string;
  id: number;
  lessonTitle?: string;
  lessonId: number;
  feedback?: string;
}

const UpcomingClass: FC<PropsType> = (props) => {
  const {
    title,
    studentInfo,
    enterLink,
    id,
    lessonId,
    lessonTitle,
    feedback,
  } = props;

  const dispatch = useDispatch();

  const handleShowFeedback = useCallback(() => {
    dispatch(upcomingClassFeedbackPending(id));
  }, [dispatch, id]);

  const handleViewLesson = useCallback(() => {
    dispatch(upcomingClassCurriculumPending(id, lessonId));
  }, [id, lessonId, dispatch]);

  return (
    <div className={cx('upcomingClass')}>
      <div className={cx('title')}>{title}</div>
      <ClassField title="Student">
        <div className={cx('block')}>
          {studentInfo}
          {' '}
          (Next lesson:
          {' '}
          {lessonTitle ? (
            <Button theme="link" text={lessonTitle} />
          ) : (
            <Button theme="link" text="Views lesson" onClick={handleViewLesson} />
          )}
        </div>
      </ClassField>
      <ClassField title="Last feedback">
        <div className={cx('block')}>
          {feedback || <Button text="Show feedback" onClick={handleShowFeedback} />}
        </div>
        <Button
          theme="primary"
          text="Enter classroom"
          href={enterLink}
        />
      </ClassField>
    </div>
  );
};

export default memo(UpcomingClass);
