import React, { useCallback, memo, FC } from 'react';
import { useSelector, useDispatch } from 'react-redux';

import SectionTitle from 'components/SectionTitle';
import Button from 'components/Button';

import { CompletedClassesType } from 'types/dashboardType';
import { getCompletedClassesLoading, getNextCompletedClassesLink } from 'store/dashboard/selectors';
import { moreCompletedClassesPending } from 'store/dashboard/actions';
import CompletedClass from './CompletedClass';

interface PropsType {
  completedClasses: CompletedClassesType;
}

const CompletedClasses: FC<PropsType> = ({ completedClasses = [] }) => {
  const dispatch = useDispatch();
  const loading = useSelector(getCompletedClassesLoading);
  const nextCompletedClassesLink = useSelector(getNextCompletedClassesLink);

  const handleLoadMoreNextClasses = useCallback(() => {
    dispatch(moreCompletedClassesPending(nextCompletedClassesLink));
  }, [nextCompletedClassesLink, dispatch]);

  if (!completedClasses.length) {
    return null;
  }

  return (
    <>
      <SectionTitle title="Completed Classes" loading={loading} />
      {completedClasses.map((completedClass) => (
        <CompletedClass {...completedClass} key={completedClass.id} />
      ))}
      <Button
        text="Further classes"
        disabled={!nextCompletedClassesLink}
        onClick={handleLoadMoreNextClasses}
      />
    </>
  );
};

export default memo(CompletedClasses);
