import React, {
  FC,
  memo,
  useState,
  useCallback,
} from 'react';
import { Link } from 'react-router-dom';
import classnames from 'classnames/bind';
import { FaVideo } from 'react-icons/fa';

import Button from 'components/Button';
import ClassField from 'pages/Dashboard/ClassField';
import { classStatus } from 'constants/class';

import styles from './styles.module.scss';

const cx = classnames.bind(styles);

export interface PropsType {
  title: string;
  studentInfo: string;
  lessonTitle: string;
  credit: string;
  status: string;
  id: number;
  replayDataLink?: string;
}

const CompletedClass: FC<PropsType> = (props) => {
  const {
    title,
    studentInfo,
    lessonTitle,
    credit,
    status,
    replayDataLink,
  } = props;

  const [showFeedback, setShowFeedback] = useState(false);

  const handleShowFeedback = useCallback(() => {
    setShowFeedback(true);
  }, []);

  return (
    <div className={cx('completedClass')}>
      <div className={cx('title')}>
        {title}
        <div
          className={cx(
            'status',
            { completed: status === classStatus.completed },
          )}
        >
          {status}
        </div>
        {credit && credit !== '0.00' && (
          <span className={cx('credit')}>
            {' '}
            credit:
            {credit}
            {' '}
            classes
          </span>
        )}
        {!credit && (
          <span className={cx('.processing')}> processing payment...</span>
        )}
      </div>
      <ClassField title="lesson">
        {status === classStatus.completed && !!replayDataLink && (
          <Link className={cx('dataLink')} to={replayDataLink}>
            <FaVideo color="#00adcb" />
          </Link>
        )}
        <Button theme="link" text={lessonTitle} />
      </ClassField>
      <ClassField title="student">
        {studentInfo}
      </ClassField>
      <ClassField title="feedback">
        {showFeedback
          ? 'N/A'
          : <Button text="Show feedback" onClick={handleShowFeedback} />}
      </ClassField>
    </div>
  );
};

export default memo(CompletedClass);
