import React, { FC, useEffect } from 'react';
import classnames from 'classnames/bind';

import { useSelector, useDispatch } from 'react-redux';
import { Helmet } from 'react-helmet';

import Container from 'components/Container';
import TopLoading from 'components/TopLoading';
import { getDashboardInfo } from 'store/dashboard/selectors';
import { dashboardPending } from 'store/dashboard/actions';
import ProfileInformation from './ProfileInformation';
import Statistics from './Statistics';
import PrepareForClasses from './PrepareForClasses';
import Feedback from './Feedback';
import NextClass from './NextClass';
import UpcomingClasses from './UpcomingClasses';
import CompletedClasses from './CompletedClasses';

import styles from './styles.module.scss';

const cx = classnames.bind(styles);

const Dashboard: FC = () => {
  const dispatch = useDispatch();
  const {
    profileInformation,
    statistics,
    feedback,
    nextClass,
    upcomingClasses,
    completedClasses,
    topLoading,
  } = useSelector(getDashboardInfo);

  useEffect(() => {
    dispatch(dashboardPending());
  }, [dispatch]);

  return (
    <>
      <Helmet>
        <title>Novakid - Main page</title>
      </Helmet>
      <TopLoading loading={topLoading} />
      <div className={cx('dashboard')}>
        <Container>
          <ProfileInformation {...profileInformation} />
          <Statistics {...statistics} />
          <Feedback {...feedback} />
          {nextClass ? (
            <NextClass {...nextClass} />
          ) : (
            <PrepareForClasses />
          )}
          <UpcomingClasses upcomingClasses={upcomingClasses} />
          <CompletedClasses completedClasses={completedClasses} />
        </Container>
      </div>
    </>
  );
};

export default Dashboard;
