import React, { FC, memo } from 'react';
import classnames from 'classnames/bind';
import { useSelector } from 'react-redux';

import SectionTitle from 'components/SectionTitle';
import Tooltip from 'components/Tooltip';

import { getStatisticsLoading } from 'store/dashboard/selectors';
import statisticsHints from './statisticsHints';

import styles from './styles.module.scss';

const cx = classnames.bind(styles);

export interface PropsType {
  students?: number;
  trial?: number;
  regular?: number;
  rateStudents?: number;
  level?: string;
}

const Statistics: FC<PropsType> = (props) => {
  const {
    students = 0,
    trial = 0,
    regular = 0,
    rateStudents = 0,
    level = '',
  } = props;

  const loading: boolean = useSelector(getStatisticsLoading);
  const showLevel: boolean = rateStudents >= 10;

  return (
    <>
      <SectionTitle title="Statistics for last 30-days" loading={loading} />
      <div className={cx('statistics')}>
        <div className={cx('section')}>
          <div className={cx('header')}>
            CONVERSIONS
          </div>
          <div className={cx('col')}>
            {showLevel ? (
              <div className={cx('row', 'value')}>{level}</div>
            ) : (
              <div className={cx('row', 'title')}>
                <Tooltip arrow title={statisticsHints.collectingData}>
                  <span>Collecting data</span>
                </Tooltip>
              </div>
            )}
            <div className={cx('row', 'title')}>
              <Tooltip arrow title={statisticsHints.performance}>
                <span>Performance</span>
              </Tooltip>
            </div>
          </div>
          <div className={cx('col')}>
            <div className={cx('row', 'value')}>{students}</div>
            <div className={cx('row', 'title')}>
              <Tooltip arrow title={statisticsHints.students}>
                <span>Students</span>
              </Tooltip>
            </div>
          </div>
        </div>
        <div className={cx('section')}>
          <div className={cx('header')}>
            CLASSES COMPLETED
          </div>
          <div className={cx('col')}>
            <div className={cx('row', 'value')}>{trial}</div>
            <div className={cx('row', 'title')}>
              <Tooltip arrow title={statisticsHints.trial}>
                <span>Trial</span>
              </Tooltip>
            </div>
          </div>
          <div className={cx('col')}>
            <div className={cx('row', 'value')}>{regular}</div>
            <div className={cx('row', 'title')}>
              <Tooltip arrow title={statisticsHints.regular}>
                <span>Regular</span>
              </Tooltip>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default memo(Statistics);
