import React, { memo, FC } from 'react';
import { useSelector } from 'react-redux';

import SectionTitle from 'components/SectionTitle';

import { UpcomingClassesType } from 'types/dashboardType';
import { getUpcomingClassesLoading } from 'store/dashboard/selectors';
import UpcomingClass from './UpcomingClass';

interface PropsType {
  upcomingClasses: UpcomingClassesType
}

const UpcomingClasses: FC<PropsType> = ({ upcomingClasses = [] }) => {
  const loading: boolean = useSelector(getUpcomingClassesLoading);

  if (!upcomingClasses.length) {
    return null;
  }

  return (
    <>
      <SectionTitle title="Upcoming Classes" loading={loading} />
      {upcomingClasses.map((upcomingClassProps) => (
        <UpcomingClass
          {...upcomingClassProps}
          key={upcomingClassProps.id}
        />
      ))}
    </>
  );
};

export default memo(UpcomingClasses);
