import React, { FC, memo } from 'react';
import classnames from 'classnames/bind';
import { useSelector } from 'react-redux';

import SectionTitle from 'components/SectionTitle';
import Button from 'components/Button';
import ClassField from 'pages/Dashboard/ClassField';
import useTimer from 'hooks/useTimer';
import { getNextClassLoading } from 'store/dashboard/selectors';
import PrepareForClasses from '../PrepareForClasses';

import styles from './styles.module.scss';

const cx = classnames.bind(styles);

export interface PropsType {
  title?: string;
  studentInfo?: string;
  enterLink?: string;
  lessonTitle?: string;
  id: number;
  lessonId: number;
  feedback?: string;
  classStartTime?: string;
}

const NextClass: FC<PropsType> = (props) => {
  const {
    title,
    studentInfo,
    enterLink,
    lessonTitle,
    feedback,
    classStartTime = '',
  } = props;

  const loading: boolean = useSelector(getNextClassLoading);
  const timer = useTimer(classStartTime);

  return (
    <>
      <SectionTitle title="Next Class" loading={loading} />
      <div className={cx('nextClass')}>
        <div className={cx('title')}>
          {title}
          {timer && `, starts in ${timer}`}
        </div>
        <ClassField title="Lesson">
          {lessonTitle && <Button theme="link" text={lessonTitle} />}
        </ClassField>
        <ClassField title="Student">
          <div className={cx('section')}>{studentInfo}</div>
        </ClassField>
        {feedback && (
          <ClassField title="Last feedback">
            <div className={cx('section')}>{feedback}</div>
          </ClassField>
        )}
        <ClassField title="">
          <Button theme="primary" text="Enter classroom" href={enterLink} />
        </ClassField>
        <ClassField title="Attention">
          <PrepareForClasses isContainedInNextClass />
        </ClassField>
      </div>
    </>
  );
};

export default memo(NextClass);
