import React, { FC, memo } from 'react';
import classnames from 'classnames/bind';
import { useSelector } from 'react-redux';

import AccountHealth from 'components/AccountHealth';
import SectionTitle from 'components/SectionTitle';

import { Support } from 'types/common';
import { getProfileInfoLoading } from 'store/dashboard/selectors';
import avatarEmptySrc from 'assets/avatar-empty.png';

import styles from './styles.module.scss';

const cx = classnames.bind(styles);

export interface PropsType {
  name?: string;
  imgSrc?: string;
  education?: string;
  experience?: string;
  classCost?: string;
  balance?: string;
  accountHealth?: number;
  support?: Support;
}

const ProfileInformation: FC<PropsType> = (props) => {
  const {
    name,
    imgSrc,
    education,
    experience,
    classCost,
    balance,
    accountHealth,
    support,
  } = props;

  const isLoading: boolean = useSelector(getProfileInfoLoading);

  return (
    <>
      <SectionTitle title="Profile information" loading={isLoading} />
      <div className={cx('profileInformation')}>
        <div className={cx('imgContainer')}>
          <img alt={name} src={imgSrc || avatarEmptySrc} className={cx('img')} />
        </div>
        <div className={cx('userInfo')}>
          <h4 className={cx('name')}>
            {name}
          </h4>
          <div className={cx('line')}>
            <strong>Education: </strong>
            {education}
          </div>
          <div className={cx('line')}>
            <strong>Experience: </strong>
            {experience}
          </div>
          <div className={cx('line')}>
            <strong>Class cost: </strong>
            {classCost}
          </div>
          <div className={cx('line')}>
            <strong>Balance: </strong>
            {balance}
          </div>
          <div className={cx('line')}>
            <strong>Account health: </strong>
            <AccountHealth heartsLength={accountHealth} />
          </div>
          <div className={cx('line')}>
            <strong>Support team: </strong>
            Teacher support tel.
            {support?.phone1 && (
            <span>
              <a href={`tel:${support.phone1}`}>
                {' '}
                {support.phone1}
                ,
              </a>
            </span>
            )}
            {support?.phone2 && (
            <span>
              <a href={`tel:${support.phone2}`}>
                {' '}
                {support.phone2}
              </a>
            </span>
            )}
            {support?.period && (
            <span>
              {' '}
              {support.period}
            </span>
            )}
            {support?.mail && (
            <span>
              {' '}
              E-Mail:
              <a href={`mailto:${support.mail}`}>
                {' '}
                {support.mail}
              </a>
            </span>
            )}
          </div>
        </div>
      </div>
    </>
  );
};

ProfileInformation.defaultProps = {
  education: 'N/A',
  experience: 'N/A',
  classCost: '$0.00',
  accountHealth: 0,
  support: {},
};

export default memo(ProfileInformation);
