import React, {
  FC,
  memo,
  useState,
  useCallback,
  useMemo,
} from 'react';
import classnames from 'classnames/bind';

import SectionTitle from 'components/SectionTitle';
import Button from 'components/Button';
import Modal from 'components/Modal';
import ConfirmModal from 'components/ConfirmModal';
import InternetSpeedTest from 'components/InternetSpeedTest';

import styles from './styles.module.scss';

const cx = classnames.bind(styles);

interface PropsType {
  isContainedInNextClass?: boolean;
}

const PrepareForClasses: FC<PropsType> = (props) => {
  const {
    isContainedInNextClass = false,
  } = props;

  const [internetSpeedTestModalOpen, setInternetSpeedTestModalOpen] = useState(false);
  const [deviceTestModalOpen, setDeviceTestModalOpen] = useState(false);
  const [testIsRunning, setTestIsRunning] = useState(false);
  const [confirmModalOpen, setConfirmModalOpen] = useState(false);

  const handleToggleConfirmModal = useCallback(() => {
    setConfirmModalOpen((prevOpen) => !prevOpen);
  }, []);

  const handleToggleInternetSpeedTestModal = useCallback(() => {
    if (testIsRunning) {
      handleToggleConfirmModal();
      return;
    }
    setInternetSpeedTestModalOpen((prevOpen) => !prevOpen);
  }, [handleToggleConfirmModal, testIsRunning]);

  const handleToggleDeviceTestModal = useCallback(() => {
    setDeviceTestModalOpen((prevOpen) => !prevOpen);
  }, []);

  const handleToggleRunningTest = useCallback(() => {
    setTestIsRunning((prevState) => !prevState);
  }, []);

  const body = useMemo(() => (
    <h1>Sometext</h1>
  ), []);

  const buttonTheme = useMemo(() => {
    return isContainedInNextClass ? '' : 'primary';
  }, [isContainedInNextClass]);

  const confirmModalCallback = useCallback(() => {
    if (internetSpeedTestModalOpen) {
      setInternetSpeedTestModalOpen(false);
    }

    if (deviceTestModalOpen) {
      setDeviceTestModalOpen(false);
    }

    setTestIsRunning(false);
    handleToggleConfirmModal();
  }, [deviceTestModalOpen, handleToggleConfirmModal, internetSpeedTestModalOpen]);

  return (
    <>
      {!isContainedInNextClass && (
        <SectionTitle title="Prepare for Classes" loading={false} />
      )}
      <div className={cx('prepareForClasses')}>
        {!isContainedInNextClass && (
          <p className={cx('title')}>
            <strong>Attention</strong>
          </p>
        )}
        <p className={cx('text')}>
          We recommend to run a test of your Internet connection, web camera,
          sound and a microphone in advance to avoid technical issues during a class.
        </p>
        <div className={cx('buttons', {
          nextClass: isContainedInNextClass,
        })}
        >
          <Button
            theme={buttonTheme}
            text="Run a test"
            onClick={handleToggleDeviceTestModal}
          />
          {' '}
          <Button
            theme={buttonTheme}
            text="Internet speed test"
            onClick={handleToggleInternetSpeedTestModal}
          />
        </div>
      </div>
      <Modal
        title="Equipment test"
        onClose={handleToggleDeviceTestModal}
        open={deviceTestModalOpen}
      >
        {body}
      </Modal>
      <Modal
        title="Internet speed test"
        onClose={handleToggleInternetSpeedTestModal}
        open={internetSpeedTestModalOpen}
      >
        <InternetSpeedTest
          toggleRunningTest={handleToggleRunningTest}
          finishTest={handleToggleInternetSpeedTestModal}
        />
      </Modal>
      <ConfirmModal
        open={confirmModalOpen}
        confirmTitle="The test will be canceled."
        onClose={handleToggleConfirmModal}
        confirm={confirmModalCallback}
      />
    </>
  );
};

export default memo(PrepareForClasses);
