import { AppStateType } from 'types';
import DashboardState, { EstimationTags } from 'types/dashboardType';

export const getDashboardInfo = (state: AppStateType): DashboardState => state.dashboard;

export const getProfileInfoLoading = (state: AppStateType): boolean => (
  state.dashboard.userLoading
  || state.dashboard.teacherLoading
  || state.dashboard.regionLoading
);

export const getStatisticsLoading = (state: AppStateType): boolean => (
  state.dashboard.statisticsLoading
  || state.dashboard.tagsLoading
);

export const getTags = (state: AppStateType): EstimationTags => state.dashboard.tags;

export const getUpcomingClassesLoading = (state: AppStateType): boolean => (
  state.dashboard.upcomingClassesLoading
);

export const getCompletedClassesLoading = (state: AppStateType): boolean => (
  state.dashboard.completedClassesLoading
);

export const getNextCompletedClassesLink = (state: AppStateType): null | string => (
  state.dashboard.nextCompletedClassesLink
);

export const getNextClassLoading = (state: AppStateType): boolean => (
  state.dashboard.nextClassCurriculumLoading
  || state.dashboard.nextClassFeedbackLoading
);
