import React, { FC, memo } from 'react';
import classnames from 'classnames/bind';
import { useSelector } from 'react-redux';
import { getTags } from 'store/dashboard/selectors';
import { Tag } from 'types/dashboardType';

import styles from './styles.module.scss';

const cx = classnames.bind(styles);

export interface PropsType {
  valueUp?: number;
  thumbUpTags?: Tag[];
  thumbDownTags?: Tag[];
}

const Feedback: FC<PropsType> = (props) => {
  const { valueUp = 100, thumbUpTags, thumbDownTags } = props;
  const tags = useSelector(getTags);

  const hintText = valueUp > 90
    ? 'Pay attention to negative feedbacks!'
    : 'This is very bad!';

  if (!thumbUpTags?.length || !thumbDownTags?.length) {
    return null;
  }

  return (
    <div className={cx('feedback')}>
      <span className={cx('title')}>FEEDBACK</span>
      <div className={cx('content')}>
        <div className={cx('feedbackSection', 'indicatorContainer')}>
          <div className={cx('indicator')}>
            <span className={cx('indicatorValue', 'indicatorValueUp')}>
              {valueUp}
              %
            </span>
            <span className={cx('indicatorValue', 'indicatorValueDown')}>
              {100 - valueUp}
              %
            </span>
            <div className={cx('indicatorProgress')}>
              <div className={cx('indicatorBar')} style={{ width: `${valueUp}%` }} />
            </div>
            {valueUp <= 95 && (
            <div className={cx('indicatorHint')}>
              {hintText}
            </div>
            )}
          </div>
        </div>
        <div className={cx('feedbackSection')}>
          <span className={cx('sectionTitle')}>POSITIVE TAGS</span>
          {thumbUpTags?.length ? (
            thumbUpTags.map(({ value, id }) => (
              <div className={cx('feedbackTag')} key={id}>
                <span className={cx('value')}>
                  {value}
                  %
                </span>
                <span className={cx('title')}>{tags[id]}</span>
              </div>
            ))
          ) : (
            <div className={cx('feedbackTag')}>N/A</div>
          )}
        </div>
        <div className={cx('feedbackSection')}>
          <span className={cx('sectionTitle')}>NEGATIVE TAGS</span>
          {thumbDownTags?.length ? (
            thumbDownTags.map(({ value, id }) => (
              <div className={cx('feedbackTag')} key={id}>
                <span className={cx('value')}>
                  {value}
                  %
                </span>
                <span className={cx('title')}>{tags[id]}</span>
              </div>
            ))
          ) : (
            <div className={cx('feedbackTag')}>N/A</div>
          )}
        </div>
      </div>
    </div>
  );
};

export default memo(Feedback);
